.employer{margin-bottom:3rem}.employer__box{display:flex;column-gap:1rem}.employer__left{flex:1 1 20%;padding:2rem;background-color:var(--white-100);display:flex;flex-direction:column;row-gap:2rem}.employer__right{flex:1 1 80%;padding:0 1.5rem}.employer__right .des-brend__title{pointer-events:none}.employer__right .des-brend__title-link{pointer-events:none;user-select:none;font-size:3.2rem;font-weight:700;font-family:"ProximaNovaCond"}.employer__img{margin:0 auto}.employer__img[data-valid=true]{display:none}.employer__content p{margin-bottom:1.5rem}.employer__content ul li{position:relative}.employer__content ul li::before{content:"—";display:block;position:absolute;right:100%;padding-right:6px}.emp-list__item{text-align:center}.emp-list__item:not(:last-child){margin-bottom:1.5rem}.emp-list__name{margin-bottom:1rem}.emp-list__link{color:var(--blue-100)}.emp-list__title{font-weight:700}.emp-buttons__item:not(:last-child){margin-bottom:1rem}.emp-buttons__btn{border:1px solid var(--gray-100);padding:1rem;width:100%;border-radius:5px;transition:background-color .5s ease-in-out}.emp-buttons__btn:hover{background-color:var(--gray-200)}@media(max-width: 700px){.employer__box{display:flex;flex-direction:column}body{font-size:1.2rem}.descriptions__btn-prev{margin-bottom:.5rem;width:3rem;height:3rem;margin-left:1rem}}